	module.exports = {"data":{"page":{"id":"/Users/drugsism/Documents/Persnal/sopesnote/content/pages/1--about/index.md absPath of file >>> MarkdownRemark","html":"<p>블로그를 수정중입니다.\n<a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">Getsby-Starter from Github</a>을 stater로 사용했습니다.</p>\n<h3>Standard features:</h3>\n<ul>\n<li>Markdown 파일 (게시물, 페이지 및 부품) 에서 손쉽게 편집 할 수있는 콘텐츠</li>\n<li>SEO (사이트 맵 생성, robot.txt, 메타 및 OpenGraph 태그)</li>\n<li>SNS 공유 (Twitter, Facebook, Google, LinkedIn)</li>\n<li>댓글 (Facebook)</li>\n<li>Images lazy loading and webp support (gatsby-image)</li>\n<li>소식 카테고리 (게시물 목록 필터링)</li>\n<li>전체 텍스트 검색 (Algolia)</li>\n<li>Contact form (Netlify form handling)</li>\n<li>RSS 피드</li>\n<li>PWA (manifest.json, 오프라인 지원, 파비콘)</li>\n<li>Google 애널리틱스</li>\n<li>Favicons 생성기 (노드 스크립트)</li>\n<li>Material UI (@next)</li>\n</ul>\n<h3>Advanced features:</h3>\n<ul>\n<li>Easy customizable base <strong>styles</strong> via <code class=\"language-text\">theme</code> object (fonts, colors, sizes)</li>\n<li><strong>Components</strong> lazy loading (social sharing, comments)</li>\n<li><strong>ESLint</strong> (google config)</li>\n<li><strong>Prettier</strong> code styling</li>\n<li>Custom webpack <code class=\"language-text\">CommonsChunkPlugin</code> settings</li>\n<li>Webpack <code class=\"language-text\">BundleAnalyzerPlugin</code></li>\n<li>Contact <strong>form validation</strong> (react-material-ui-form-validator)</li>\n</ul>\n<h2>Instructions</h2>\n<p>For detailed instructions go to <a href=\"https://dev.greglobinski.com/gatsby-starter-personal-blog/\">Front-end web development with Greg</a>.</p>","frontmatter":{"title":"About"}},"footnote":{"id":"/Users/drugsism/Documents/Persnal/sopesnote/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>this is a demo site of the <a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">gatsby-starter-personal-blog</a></li>\n<li>built by <a href=\"https://www.greglobinski.com\">greg lobinski</a></li>\n<li>GatsbyJS, ReactJs, CSS in JS - <a href=\"https://dev.greglobinski.com\">Front-end web development with Greg</a></li>\n<li>delivered by <a href=\"https://www.netlify.com/\">Netlify</a></li>\n<li>photos by <a href=\"https://unsplash.com\">unsplash.com</a></li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"..."}}}},"pathContext":{"slug":"/about/"}}

